<template>
  <div>
    <headerBar :header-data="headerData" />

    <div class="container">
      <!-- 收货地址 begin -->

      <common-list
        ref="ggcommonlist"
        class="common-list"
        :post-url="list.postUrl"
        :req-data="list.reqData"
        :data-name="list.dataName"
        @returnData="getPrizeLog($event)"
      >
        <van-swipe-cell v-for="(address,index) of addresList" :key="index" right-width="220px">
          <div class="content">
            <div class="common-main box">
              <div class="add-select">
                <div class="add-l">
                  <div class="add-icon2">
                    <i class="iconfont iconweizhi" />
                  </div>
                  <div class="add-info">
                    <div class="name">{{ address.consignee }}<span class="phone">{{ address.mobile }}</span><van-tag v-if="address.default_address ===1" type="primary">默认</van-tag></div>
                    <div class="text">{{ address.area }}</div>
                  </div>
                </div>
                <div class="add-r" @click="goAddressEdit(address)">
                  <i class="iconfont iconxiugai" />
                </div>
              </div>
            </div>
          </div>
          <template slot="right">
            <van-button square type="primary" text="设为默认" @click="DefaultDz(index)" />
            <van-button square type="danger" text="删除" @click="DeleteDz(index)" />
          </template>
        </van-swipe-cell>
      </common-list>
    </div>
    <div class="common-space" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn one fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="goAddressadd()">新建收货地址</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import commonList from '@/components/list/commonList1.vue'
import { getShdz, defaultAddress, deleteAdress } from '@/store/module.js'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'AddressList',
  components: {
    headerBar,
    commonList
  },
  // mixins: [pageRouter],
  data() {
    return {
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      addresList: [],
      isLoading: true,
      checked: true,
      fileList: [
        { url: 'https://img.yzcdn.cn/vant/leaf.jpg' }
      ],
      headerData: {
        title: '选择收货地址',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.list.postUrl = getShdz
      this.list.reqData = { pageno: 1,
        pagerow: 10
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无数据'
    },
    getPrizeLog(data) {
      this.addresList = data
    },
    goBack() {
      this.$router.back()
    },
    goAddressadd() {
      this.$router.push({
        name: 'AddressAdd',
        params: {
          title: '新增收货地址'
        }
      })
    },
    goAddressEdit(address) {
      this.$router.push({
        name: 'AddressAdd',
        params: {
          title: '修改收货地址',
          address: address
        }
      })
    },
    DeleteDz(index) {
      deleteAdress.data = {
        e_address_id: this.addresList[index].id
      }
      this.asyncApiModule(deleteAdress).then(res => {
        this.$toast.success('删除成功')
        this.$bus.$emit('refList1')
        window.scrollTo(0, 0)
      })
        .catch(() => {
          this.loading = false
        })
    },

    DefaultDz(index) {
      defaultAddress.data = {
        e_address_id: this.addresList[index].id,
        pageno: 1,
        pagerow: 1000
      }
      this.asyncApiModule(defaultAddress).then(res => {
        this.$toast.success('设置成功')
        this.$bus.$emit('refList1')
        window.scrollTo(0, 0)
      })
        .catch(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style lang="css" scoped>

</style>
